const {TOURNAMENT_STEP}  = require("../model/const");

class ManageState {
	constructor() {

	}
	getRandom(length) {
		return Math.floor(Math.random()*length);
	}
	getRandomItems(state,action) {
		let tempStore=state.store.getContent();
		let length=tempStore.length;
		let toDelete=[];
		let tempObject=[];
		let tempArray=[];
		let result={};
		let rand=this.getRandom(length);
		let newStoreNames=[];

		/*
		16강:history에 값이 없다면 store의 16개 데이터를 랜덤으로 조합하여 history에 저장, history를 출력, 선택된 데이터는 newstore에 저장
		8강: 16강에서 선택된 값 newstore에서 랜덤으로 8개를 추출하여 history에 저장, history를 출력, 선택된 데이터는 newstore에 저장
		 */

		//이전 버튼 클릭 시 마지막으로 저장된 내용으로 되돌리기
		if(action==="prev") {
			result[state.paging.getPaging()] = state.historyStore.getContent()[state.historyStore.getContent().length-1];
			Object.assign(result, {paging: state.paging.getPaging()})
		} else {

			if(state.newStore.getContent().length>0) {
				tempStore=[];
				state.newStore.getContent().map((data)=> data.map((list)=> tempStore.push(list[0])));
			}

			for(var i = 0; i < state.paging.getPaging()/TOURNAMENT_STEP; i++) {
				for(var j = 0; j < TOURNAMENT_STEP; j++) {

					rand = this.getRandom(tempStore.length);
					tempArray.push(tempStore[rand]);

					tempStore = tempStore.filter((obj) => obj.name!==tempArray[0].name);
				}

				// 중복 리스트 만들기
				newStoreNames.push(tempArray[tempArray.length-1].name);

				//선택했던 배열 이름을 사용하여 기존 데이터 중복 삭제
				toDelete = new Set(newStoreNames);
				tempStore = tempStore.filter(obj => !toDelete.has(obj.name));

				tempObject.push(tempArray);
				tempArray=[];
			}

			result[state.paging.getPaging()]= tempObject;

			result = Object.assign(result, {paging: state.paging.getPaging(),newStore:state.newStore});
		}

		return result;

	}
	selectItems(state,names) {

		let selectIndex=0;
		let nonSelectIndex=0;
		let result=[];
		let items={};

		// deep copy
		let temp = JSON.parse(JSON.stringify(state.items[state.items.paging]));

		for(var i = 0; i < names.length; i++) {
			for(var j = 0; j < temp.length; j++) {

				selectIndex=temp[j].findIndex(x => x.name === names[i]);

				if(selectIndex>-1) {

					temp[j][selectIndex]['stage']=state.paging.currentPaging;
					temp[j][selectIndex]['use']=true;

					nonSelectIndex=(selectIndex===0) ? 1 : 0;
					temp[j][nonSelectIndex]['stage'] = state.paging.currentPaging;
					temp[j][nonSelectIndex]['use'] = false;
				}
			}
		}

		items= JSON.parse(JSON.stringify(temp));
		Object.assign(items,{stage:state.paging.currentPaging});
		result=temp.map(data=> data.filter((x)=> x.use===true));
		Object.assign(result, temp.filter((x)=> x.use===true));

		return {
			newStore:result,
			historyStore:items
		}

	}
}

module.exports={
	ManageState
}
